.Department{
	font-size: 35px;
	background-color: rgb(0,45,65);
    margin: 40px 50px;
    text-decoration: none;
    display: inline-block;
    color: white;
    width: 170px;
    line-height: 160px;
    border-radius: 50%;
}
.Department:hover {
	background-color: white;
    margin: 35px 45px;
    color: rgb(0,45,65); 
    font-weight: bold;
    width: 180px;
    line-height: 170px;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}
.Subject{
	font-size: 25px;
    background-color: rgb(0,45,65);
    padding: 5px;
    margin: 15px 20px;
    text-decoration: none;
    display: inline-block;
    color: white;
    width: 500px;
    border-radius: 50px;
}
.Subject:hover {
	background-color: white;
    margin: 15px 10px;
    color: rgb(0,45,65); 
    width: 520px; 	
    font-weight: bold;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}
@media(max-width:1100px){
    .Subject{
        width: 300px;
        font-size: 18px;
        margin: 10px 5px;
    }
    .Subject:hover{
        width: 320px;
        margin: 10px 0px;
    }
    .Department{
        font-size: 25px;
        margin: 10px 10px;
        display: inline-block;
        width: 110px;
        line-height: 105px;
    }
    .Department:hover {
        margin: 5px 5px;
        width: 120px;
        line-height: 115px;
    }
}


