.Footer{
    background-color: #000;
    color: #fff;
    -webkit-box-shadow: 0px 0px 20px black;
            box-shadow: 0px 0px 20px black;
    text-align: center;
    padding-bottom: 15px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    bottom: 0;
    position: fixed;
    width: 100%;
    height: auto;
}
.Footer p{
    font-size: 1.25rem;
    margin: 15px auto;
}
.Footer span{
    font-size: 1rem;
    color: #6c757d;
}
@media(max-width:1100px){
    .Footer p{
        font-size: 1rem;
    }
    .Footer span{
        font-size: 0.9rem;
    }
}


