.Toolbar{
    background-color: #000;
    color: #fff;
    -webkit-box-shadow: 0px 0px 20px #000;
            box-shadow: 0px 0px 20px #000;
    text-align: center;
    top: 0;
    position: fixed;
    width: 100%;
    padding-top: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.container{
    width: 70%;
    margin: 10px auto;
    display: -ms-flexbox;
    display: flex;
}
@media(max-width:1100px){
    .container{
        width: 95%;
    }
}


